import React from 'react'
import { Link } from 'react-router-dom'

const facilities = () => {
    return (
        <>
        <div className="Header">
          
          <h3>Facilities</h3>
          <div>
          <Link to="/dashboard" id="backbtn">BACK</Link>
          </div>
          </div>
          
        <div className="bodypart" id="bodypart">
        <img src="https://creativeteachered.org/wp-content/uploads/2020/07/LearningBanner.gif" width="100%" height="140px" alt="Banner"></img>
        <br></br>
<p>Hostel rooms are comfortably-furnished and make for a good home away from home. Girls and boys have separate blocks and each block has a fixed capacity. There is a range of accommodation options to choose from, for students’ convenience.

In this kind of accommodation, students have access to TV room, aqua guard for hot, cold and normal drinking water and other leisure/sports activities within the hostel premises. Also includes the following facilities:
<ol type="1">
     <li>1. 24x7 Wi-Fi connectivity provided in all hostels</li>
     <li>2. CCTV cameras at entrances of all hostels and foyer inside the campus.</li>
     <li>3. 24x7 electricity and water supply in all the hostels</li>
     <li>4. Night cafeterias up to 1.00 AM</li>
     <li>5. Solar panels installed in all hostels for hot water </li>
     <li>6. Caretakers and housekeeping services are provided in all hostels during day and night</li>
     <li>7. Bike farm inside the campus for the students to buy and service their bicycle</li>
</ol>
     </p>
        </div>
        </>
    )
}

export default facilities
